html {
  box-sizing: border-box;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  min-height: 100vh;
  flex-direction: column;
  display: flex;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  outline-offset: 0;
  border: 0;
  outline: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --primary-text-color: #000;
  --secondary-text-color: #b92f2c;
  --akcent-text-color: #fff;
  --footer-text-color: #545454;
  --primary-bg-color: #f0f0f0;
  --footer-bg-color: #f7f7f7;
  --background-header-radient: linear-gradient(273.67deg, rgba(0, 0, 0, .49) -2.09%, rgba(0, 0, 0, 0) 99.83%);
  --overlay-bg-color: rgba(0, 0, 0, .2);
  --font-family: "Roboto", sans-serif;
}

